.allset {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3D9970;
  width: 100%;
  position: fixed; /* Fix the navbar at the top */
  top: 0;          /* Position it at the top of the page */
  left: 0;         /* Position it at the left edge of the screen */
  z-index: 1000;   /* Ensure the navbar stays on top of other content */
  padding: 10px 0;
}

.allnavbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
}

/* To avoid content hiding under the navbar */
body {
  padding-top: 70px; /* Adjust this value according to the navbar height */
}

  
  .leftnav h2 {
    font-size: 24px;
    color: #333;
    margin-right: 40px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    cursor: pointer;
  }
  
  .leftnav {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .item {
    background: transparent; 
    border: none;
    color: #333;
    font-size: 16px;
    padding: 10px 20px;
    cursor: pointer;
    transition: color 0.3s ease;
    border-radius: 2cap;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .item:hover {
    background-color: #d6d7d8c9;
    color: #333;
  }
  
  .rightnav {
    display: flex;
    align-items: center;
    gap: 5vmin;
  }
  .rightnav img{
    height: 24px;
    width: 24px;
  }
  .logo {
    border-radius: 50%; 
    padding: 10px; /* Padding around the logo */
    border: 0.5px solid rgba(94, 93, 93, 0.686); /* Semi-transparent border */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease, border 0.3s ease; /* Smooth transition on hover */
    cursor: pointer; /* Indicates it's clickable */
  }
  
  .logo:hover {
    background-color: rgba(204, 204, 204, 0.2); /* Softer transparent background on hover */
    border: 1px solid transparent; /* Fade the border out smoothly */
  }
  
  
  
  .signitem:hover {
    background-color: #0056b3;
  }
  
  .Continue {
    background-color: #2ecc71; 
    border: none;
    color: #fff;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 6px;
    border-radius: 2cap;
    transition: background-color 0.3s ease, transform 0.2s ease;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .Continue:hover {
    background:transparent; 
  }
  
  .Continue:active {
    background-color: #02611b; 
    transform: scale(0.98); 
  }
  
  @media (max-width: 768px) {
    .allnavbar {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .leftnav {
      margin-bottom: 20px;
    }
  
    .item, .signitem, button {
      font-size: 16px;
      padding: 8px 16px;
    }
  }
  